}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 55rem;
  display: flex;
  justify-content: center;
}
.header.fixed {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}
.header .container-text {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1.5rem;
}
.header .container-text h3 {
  color: #ffffff;
  font-weight: 300;
  font-size: clamp(2.5rem, 4.5vw, 3rem);
}

.footer {
  background-color: #333333;
  padding: 1.8rem;
}
.footer .container__footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
}
@media (width < 480px) {
  .footer .container__footer {
    gap: 3rem;
  }
}
.footer .container__footer h2 {
  width: 100%;
  color: #ffffff;
  font-weight: 500;
  text-transform: uppercase;
  font-size: clamp(1.7rem, 4.5vw, 2.2rem);
}
.footer .container__footer h2 span {
  font-weight: 700;
}
.footer .container__footer .links_footer {
  display: flex;
  gap: 1.2rem;
}
@media (width < 480px) {
  .footer .container__footer .links_footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
  }
}
.footer .container__footer .links_footer a {
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  margin: 0 1rem;
}

.container-header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
  transition: background-color 0.3s ease-in-out;
}

.nav {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (width < 768px) {
  .nav {
    justify-content: space-between;
    padding: 1.8rem;
  }
}
.nav > a {
  text-decoration: none;
}
.nav > a h2 {
  color: #ffffff;
  font-weight: 300;
  text-transform: uppercase;
  font-size: 3.1rem;
}
@media (width < 480px) {
  .nav > a h2 {
    font-size: 2rem;
  }
}
.nav > a h2 span {
  font-weight: 700;
}
.nav .container__checkbox {
  display: none;
}
@media (width < 768px) {
  .nav .container__checkbox {
    display: block;
  }
}
.nav .container__checkbox #checkbox {
  display: none;
}
.nav .container__checkbox .toggle {
  position: relative;
  z-index: 20;
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 7px;
  transition-duration: 0.5s;
}
.nav .container__checkbox .toggle .bars {
  width: 100%;
  height: 4px;
  background-color: #ffffff;
  border-radius: 3px;
}
.nav .container__checkbox .toggle #bar2 {
  transition-duration: 0.8s;
}
.nav .container__checkbox .toggle #bar1,
.nav .container__checkbox .toggle #bar3 {
  width: 70%;
}
.nav .container__checkbox #checkbox:checked + .toggle .bars {
  position: absolute;
  transition-duration: 0.5s;
}
.nav .container__checkbox #checkbox:checked + .toggle #bar2 {
  transform: scaleX(0);
  transition-duration: 0.5s;
}
.nav .container__checkbox #checkbox:checked + .toggle #bar1 {
  width: 100%;
  transform: rotate(45deg);
  transition-duration: 0.5s;
}
.nav .container__checkbox #checkbox:checked + .toggle #bar3 {
  width: 100%;
  transform: rotate(-45deg);
  transition-duration: 0.5s;
}
.nav .container__checkbox #checkbox:checked + .toggle {
  transition-duration: 0.5s;
  transform: rotate(180deg);
}
.nav .nav__links {
  display: flex;
  align-items: center;
  gap: 1.8rem;
}
@media (width < 768px) {
  .nav .nav__links {
    position: fixed;
    height: 100dvh;
    width: 50%;
    flex-direction: column;
    justify-content: center;
    gap: 3.2rem;
    font-weight: 500;
    top: 0;
    right: 0;
    background-color: #272727;
    transform: translateX(100%);
    transition: all 0.3s ease-in-out;
  }
}
.nav .nav__links .nav__link {
  text-decoration: none;
  color: #ffffff;
}

.no-scroll {
  overflow: hidden;
}

.container_switch {
  width: 197.5px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media (width < 840px) {
  .container_switch {
    width: 100px;
  }
}
@media (width < 768px) {
  .container_switch {
    display: none;
  }
}
.container_switch .desktop-switch {
  display: block;
}

.mobile-switch {
  display: none;
}
@media (width < 768px) {
  .mobile-switch {
    display: block;
  }
}

.toggle-switch {
  position: relative;
  width: 80px;
  height: 34px;
  --light: #d8dbe0;
  --dark: #28292c;
  --link: rgb(27, 129, 112);
  --link-hover: rgb(24, 94, 82);
}
@media (width < 768px) {
  .toggle-switch {
    margin-left: 0;
  }
}
.toggle-switch .switch-label {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #333333;
  border-radius: 25px;
  cursor: pointer;
  border: 3px solid var(--dark);
}
.toggle-switch .switch-label .checkbox {
  position: absolute;
  display: none;
}
.toggle-switch .switch-label .slider {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 25px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  display: flex;
  align-items: center;
}
.toggle-switch .switch-label .checkbox:checked ~ .slider {
  background-color: var(--light);
}
.toggle-switch .switch-label .slider::before {
  content: "";
  position: absolute;
  left: 6px;
  width: 25px;
  height: 90%;
  border-radius: 50%;
  -webkit-box-shadow: inset 12px -4px 0px 0px var(--light);
  box-shadow: inset 12px -4px 0px 0px var(--light);
  background-color: var(--dark);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.toggle-switch .switch-label .checkbox:checked ~ .slider::before {
  -webkit-transform: translateX(50px);
  -ms-transform: translateX(50px);
  transform: translateX(39px);
  background-color: var(--dark);
  -webkit-box-shadow: none;
  box-shadow: none;
}

.about {
